import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Redirect, useHistory } from 'react-router-dom'
import Swal from 'sweetalert2'
import banner from '../../asset/img/banner.png'
import { createAciton } from '../../Redux/action'
import { LOGIN, USER } from '../../Redux/action/type'
import socket from '../../Socket'
import Loading from '../Loading'
function Login(props) {
    const [phone, setPhone] = useState('')
    const [password, setPassword] = useState('')
    const history = useHistory()
    const user = useSelector(state => state.userReducer.profile)
    const login = useSelector(state => state.userReducer.login)
    const dataUser = JSON.parse(localStorage.getItem('user'))
    const dispatch = useDispatch()
    function onClick(e) {
        e.preventDefault()
        var data = {
            "api_passer":
            {
                "key_passer": "cus_log_in",
                "password": `${password}`,
                "phone": `${phone}`,
            }
        }
        socket.emit('/apiBlockchainFarm', data);

    }
    useEffect(() => {
        socket.on("cus_log_in", (res) => {
            console.log(res, "res");
            // setFertilizers(res.data)
            if (res.status == true) {
                if (res.data.type == "admin") {
                    // Swal.fire({
                    //     timer: 1200,
                    //     title: res.message,
                    //     icon: "success",
                    //     position: "center"
                    // })
                    dispatch(createAciton(USER, res.data))
                    dispatch(createAciton(LOGIN, true))
                    localStorage.setItem('user', JSON.stringify(res.data))

                } else {
                    Swal.fire({
                        timer: 1200,
                        title: "Người dùng không có quyền đăng nhập vào website này",
                        icon: "error",
                        position: "center"
                    })
                }

            } else {
                Swal.fire({
                    timer: 1200,
                    title: res.message,
                    icon: "error",
                    position: "center"
                })
            }
        })

    }, [])
    
    return (
        <>
            {login || dataUser != null ?
                <>
                    <Redirect to="/admin" />
                </>
                :
                <>
                    {/* <Loading/> */}
                    <div className="limiter">
                        <div className="container-login100" style={{ backgroundImage: `url(${banner})` }}>
                            <div className="wrap-login100 p-t-30 p-b-50">
                                <span className="login100-form-title p-b-41">
                                    Account Login
                         </span>
                                <div className="login100-form validate-form p-b-33 p-t-5">
                                    <div className="wrap-input100 validate-input"  data-validate="Enter username">
                                        <input className="input100" type="text" name="phone" onChange={(e) => {
                                            setPhone(e.target.value)
                                        }} style={{ border: "none", padding: " 0px 10px 0px 80px" }} placeholder="Số điện thoại" />
                                        <span className="focus-input100" data-placeholder="" />
                                    </div>
                                    <hr></hr>
                                    <div className="wrap-input100 validate-input"  data-validate="Enter password">
                                        <input className="input100" type="password" name="pass" onChange={(e) => {
                                            setPassword(e.target.value)
                                        }} style={{ border: "none", padding: " 0px 10px 0px 80px" }} placeholder="Password" />
                                        <span className="focus-input100" data-placeholder="" />
                                    </div>
                                    <div className="container-login100-form-btn m-t-32">
                                        <button className="login100-form-btn" onClick={onClick} style={{ backgroundImage: "-webkit-linear-gradient(0deg, #6bbc02 0%, #f4a700 100%)" }}>
                                            Login
                                </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div id="dropDownSelect1" >
                    </div>
                </>

            }

        </>
    )
}


export default Login

