import React from 'react'
import { Route } from 'react-router-dom'
import Footer from '../../ComponentAdmin/Footer'
import Header from '../../ComponentAdmin/Header'
import SliderBar from '../../ComponentAdmin/SliderBar'
// import '../../asset/app-assets/js/vendors.min.js'
// import '../../asset/app-assets/vendors/chartjs/chart.min.js'
// import '../../asset/app-assets/js/plugins.min.js'
// import '../../asset/app-assets/js/search.min.js'
// import '../../asset/app-assets/js/custom/custom-script.min.js'
// import '../../asset/app-assets/js/scripts/customizer.min.js'
// import '../../asset/app-assets/js/scripts/dashboard-ecommerce.min.js'
export const LayoutHeader = (props) => {
    return (
        <>
            <Header />
            <SliderBar />
            {props.children}
            <Footer />
            {/* START LOADER */}
        </>
    )
}
export const AdminTemplate = ({ Component, ...rest }) => {
    return (
        <Route {...rest} render={(props) => {
            return (
                <LayoutHeader >
                    <Component {...props} />
                </LayoutHeader>
            )
        }} />
    )
}