import React, { useEffect, useState } from 'react'
import { port } from '../../port'
import socket from '../../Socket'

function Fertilizer(props) {
    const [fertilizers, setFertilizers] = useState([])
    console.log(fertilizers);
    const elementFertilizers = fertilizers.map((item, index) => {
        return (
            <>
                <tr>
                    <td>
                        {index + 1}
                    </td>
                    <td> {item.Name} </td>
                    <td> <img src={`${port}${item.Images}`} style={{ borderRadius: "25%", width: "40px", height: "40px" }} alt="" /></td>
                    <td >  {item?.Detail.length > 44 ? item.Detail?.slice(0, 44) + "...." : item?.Detail} </td>
                    <td>
                        <button className="btn btn-outline-danger m-2">Xóa</button>
                        <button className="btn btn-outline-primary m-2">Sửa</button>
                    </td>
                </tr>
            </>
        )
    })
    function deleteFertilizer(id) {

    }
    useEffect(() => {
        var data = {
            "api_passer":
            {
                "key_passer": "fertilizers",
            }
        }
        socket.emit('/apiBlockchainFarm', data);
        socket.on("fertilizers", (res) => {
            setFertilizers(res.data)
        })
    }, [])
    return (
        <>
            <div id="main">
                <div className="col s12 m6 l6">
                    <div className="card-panel no-pad">
                        <div className="row no-pad">
                            <div className="col s12 pad-1">
                                <table className="highlight centered">
                                    <thead>
                                        <tr>
                                            <th>STT</th>
                                            <th>Tên</th>
                                            <th>Hình</th>
                                            <th>Mô tả</th>
                                            <th>Thao tác</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {elementFertilizers}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </>
    )
}
export default Fertilizer

