import React from 'react'
import { Link, NavLink } from 'react-router-dom'
export default function SliderBar() {
    return (
        <aside className="sidenav-main nav-expanded nav-lock nav-collapsible sidenav-dark sidenav-active-rounded">
            <div className="brand-sidebar">
                <h1 className="logo-wrapper"><a className="brand-logo darken-1" href="index-2.html"><img className="hide-on-med-and-down " src="../../../app-assets/images/logo/materialize-logo.png" alt="materialize logo" /><img className="show-on-medium-and-down hide-on-med-and-up" src="../../../app-assets/images/logo/materialize-logo-color.png" alt="materialize logo" /><span className="logo-text hide-on-med-and-down">Materialize</span></a><a className="navbar-toggler" href="#"><i className="material-icons">radio_button_checked</i></a></h1>
            </div>
            <ul className="sidenav sidenav-collapsible leftside-navigation collapsible sidenav-fixed menu-shadow" id="slide-out" data-menu="menu-navigation" data-collapsible="accordion">
                <li className="navigation-header"><a className="navigation-header-text">Applications</a><i className="navigation-header-icon material-icons">more_horiz</i>
                </li>
                <li className="bold">
                    <Link className="waves-effect waves-cyan " to="/"><i className="material-icons">mail_outline</i>
                        <span className="menu-title" data-i18n="Mail">Trang chủ</span>
                    </Link>
                </li>
                <li className="bold">
                    <NavLink className="waves-effect waves-cyan " to="/admin/education">
                        <i className="material-icons">chat_bubble_outline</i>
                        <span className="menu-title" data-i18n="Chat">Education</span>
                    </NavLink>
                </li>
                <li className="bold">
                    <NavLink className="waves-effect waves-cyan " to="/admin/fertilizers">
                        <i className="material-icons">check</i>
                        <span className="menu-title" data-i18n="ToDo">Phân bón</span>
                    </NavLink>
                </li>
                <li className="bold">
                    <NavLink className="waves-effect waves-cyan " to="/admin/user">
                        <i className="material-icons">format_list_bulleted</i>
                        <span className="menu-title" data-i18n="Kanban">Người dùng</span>
                    </NavLink>
                </li>
            </ul>
            <div className="navigation-background" /><a className="sidenav-trigger btn-sidenav-toggle btn-floating btn-medium waves-effect waves-light hide-on-large-only" href="#" data-target="slide-out"><i className="material-icons">menu</i></a>
        </aside>

    )
}
