import React, { useEffect, useState } from 'react'
import { NavLink } from 'react-router-dom';
import Pagination from '../../ComponentAdmin/Pagination';
import { port } from '../../port'
import socket from '../../Socket'
import './style.scss'
function User(props) {
    const [getListUser, setGetListUser] = useState([])
    const [limit, setLimit] = useState(8)
    const [offset, setOffset] = useState(0)
    const [page,setPage] = useState(1)
    const [total, setTotal] = useState(0)
    const elementGetListUser = getListUser?.map((item, index) => {
        return (
            <>
                <tr>
                    <td>
                        {index + 1}
                    </td>
                    <td>
                        {item.phone}
                    </td>
                    <td>
                        {item.role}
                    </td>
                    <td>
                        {item.city}
                    </td>
                    <td>
                        {item.district}
                    </td>
                    <td>
                        {item.ward}
                    </td>
                    <td>
                        {item.productName}
                    </td>
                    <td>
                        {item.brandName}
                    </td>
                    <td>
                        {item.type}
                    </td>
                    <td>
                        <button className="btn btn-outline-danger m-2">Xóa</button>
                        <button className="btn btn-outline-primary m-2">Sửa</button>
                    </td>
                </tr>
            </>
        )
    })
    function testSetPage(page) {
        setPage(page)
    }
    useEffect(() => {
        var data = {
            "api_passer":
            {
                "key_passer": "getListUser",
                "limit": limit,
                "offset": offset,
            }
        }
        socket.emit('/apiBlockchainFarm', data);
        socket.on("getListUser", (res) => {
            console.log(res, "Data");
            setGetListUser(res.data)
            setTotal(res.total)
        })
    }, [])
    return (
        <>
            <div id="main">
                <div className="m-3">
                    <NavLink className="btn btn-success" to="/admin/user/add">Thêm người dùng</NavLink>
                </div>
                <div className="col s12 m6 l6">
                    <div className="card-panel no-pad">
                        <div className="row no-pad">
                            <div className="col s12 pad-1">
                                <table className="highlight centered">
                                    <thead>
                                        <tr>
                                            <th>STT</th>
                                            <th>Số điện thoại</th>
                                            <th>Role</th>
                                            <th>Thành phố</th>
                                            <th>Quận</th>
                                            <th>Phường</th>
                                            <th>Sản phẩm</th>
                                            <th>Tên thương hiệu</th>
                                            <th>Type</th>
                                            <th>Thao tác</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {elementGetListUser}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="container flex my-3">
                    <Pagination total={Math.ceil(total / 8)} page={page} setPage={setPage} />

                </div>
            </div>
        </>
    )
}
export default User

