import React, { useState } from 'react'
import './style.scss'
export default function Pagination(props) {
    const { total } = props
    let array = []
    for (let i = 1; i <= total; i++) {
        array.push(i)
    }
    return (
        <>
            <button className="page" onClick={()=> props.testSetPage(props.page - 1)}>Sau</button>
            {array.map((item, index) => {
                return (
                    <button className={`page ${props.page == item ? `active` : ""}`} onClick={()=> props.testSetPage(item)}>{item}</button>
                )
            })}
            <button className="page" onClick={()=> props.testSetPage(props.page + 1)}>Tiếp</button>
        </>
    )
}
