import { useSelector } from 'react-redux';
import { Route, Switch } from 'react-router-dom';
import './App.css';
import Education from './Screens/Education';
import Fertilizers from './Screens/Fertilizers';
import HomeAdmin from './Screens/HomeAdmin';
import Login from './Screens/Login';
import Test from './Screens/Test';
import User from './Screens/User';
import './scss/style.scss';
import { AdminTemplate } from './Template/AdminTemplate';
function App() {
  const user = useSelector(state => state.userReducer.profile)
  const login = useSelector(state => state.userReducer.login)
  const dataUser = JSON.parse(localStorage.getItem('user'))

  return (
    <Switch>
   
      {login || dataUser != null ?
        <>
          <Route path="/tronlink" component={Test} />
          <AdminTemplate path="/admin" exact Component={HomeAdmin} />
          <AdminTemplate path="/admin/education" exact Component={Education} />
          <AdminTemplate path="/admin/fertilizers" exact Component={Fertilizers} />
          <AdminTemplate path="/admin/user" exact Component={User} />

          <Route path="/" exact component={Login} />
        </>
        :
        <>
          <Route path="/" exact component={Login} />


        </>
      }
    </Switch>
  );
}
export default App;
